import React, { useState } from 'react';
import { HashRouter, Route, Link } from "react-router-dom";

import One from '../assets/one.jpeg'
import Two from '../assets/two.jpeg'
import Three from '../assets/three.jpeg'
import Four from '../assets/four.jpeg'

import Resume from '../assets/resume.pdf'

import App from '../App'

import firebase from '../Firebase'

const Home = () => {

  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [email, setEmail] = useState('')
  const [message, setMessage] = useState('')

  const handleSubmit = (e) => {
    e.preventDefault()

    firebase.firestore().collection('messages')
      .add({
        firstName: firstName,
        lastName: lastName,
        email: email,
        message: message
      }).catch((err) => {
        alert(err.message)
      })

      setFirstName('')
      setLastName('')
      setEmail('')
      setMessage('')
  }

  return (
    <div className='home'>
      <section className='home-hero'>
        <section className='home-hero-one'>
          <h1 className='home-hero-one-title font_sans-serif font_regular'>Oscar Lee</h1>
          <h2 className='home-hero-one-subtitle font_sans-serif font_bold'>Front-end Developer</h2>
        </section>
        <section className='home-hero-two'>
          <section className='home-hero-two-one'>
            <p className='home-hero-two-one-paragraph font_sans-serif font_regular'>Front-end developer specializing in developing web pages and dynamic web applications. See my featured projects below, or <a href={ Resume } download>download my resume here</a>.</p>
          </section>
          <section className='home-hero-two-two'>
            <span className='home-hero-two-two-span primary font_sans-serif font_medium' onClick={ scrollToProjects }>View projects</span>
            <span className='home-hero-two-two-span secondary font_sans-serif font_medium' onClick={ scrollToContact }>Contact me</span>
          </section>
        </section>
      </section>
      <section className='home-projects' id='projects'>
        <ul className='home-projects-list'>
          <li className='home-projects-list-item'>
            <img src={ One } alt='project one' className='home-projects-list-item-image' />
            <p className='font_bold'>Dashboard</p>
          </li>
          <li className='home-projects-list-item'>
            <img src={ Two } alt='project two' className='home-projects-list-item-image' />
            <p className='font_bold'>bnnr</p>
          </li>
          <li className='home-projects-list-item'>
            <img src={ Three } alt='project three' className='home-projects-list-item-image' />
            <p className='font_bold'>ecom</p>
          </li>
          <li className='home-projects-list-item'>
            <img src={ Four } alt='project four' className='home-projects-list-item-image' />
            <p className='font_bold'>auth</p>
          </li>
        </ul>
      </section>
      <section className='home-contact' id='contact'>
        <h2 className='home-contact-title font_medium'>Contact me</h2>
        <form className='home-contact-form' onSubmit={ handleSubmit }>
          <div className='home-contact-form-field'>
            <p className='home-contact-form-field-label font_regular'>First name</p>
            <input type='text' placeholder='John' value={firstName} onChange={(e) => setFirstName(e.target.value)} className='home-contact-form-field-input' required/>
          </div>
          <div className='home-contact-form-field'>
            <p className='home-contact-form-field-label font_regular'>Last name</p>
            <input type='text' placeholder='Doe' value={lastName} onChange={(e) => setLastName(e.target.value)} className='home-contact-form-field-input' required/>
          </div>
          <div className='home-contact-form-field'>
            <p className='home-contact-form-field-label font_regular'>Email address</p>
            <input type='email' placeholder='you@company.com' value={email} onChange={(e) => setEmail(e.target.value)} className='home-contact-form-field-input' required/>
          </div>
          <div className='home-contact-form-field'>
            <p className='home-contact-form-field-label font_regular'>Message</p>
            <textarea placeholder='Lorem ipsum dolor sit amet' value={message} onChange={(e) => setMessage(e.target.value)} className='home-contact-form-field-input' required></textarea>
          </div>
          <button className='home-contact-form-field-input font_medium'>Send message</button>
        </form>
      </section>
      <section className='home-footer'>
        <div className='home-footer-one'>
          <ul className='home-footer-one-list'>
            <li>GitHub <span className='font_medium'>@oscarlee1</span></li>
            <li>Email <span className='font_medium'>imoscarlee1@gmail.com</span></li>
          </ul>
        </div>
        <div className='home-footer-two'>
          <p>Oscar Lee</p>
          <p>&copy; Oscar Lee 2022</p>
        </div>
      </section>
    </div>
  );
}

function scrollToProjects() {
  window.scrollTo({ top: document.getElementById('projects').offsetTop - 48, behavior: 'smooth' })
}

function scrollToContact() {
  window.scrollTo({ top: document.getElementById('contact').offsetTop, behavior: 'smooth' })
}

export default Home;
